@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);

.sidebar {
  position: fixed;
  background-color: #49274A;
  transition: width 600ms ease;
  z-index: 10000;
}

.side-nav {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100%;
}

.item {
  width: 100%;
}

.item:last-child {
  margin-top: auto;
}

.site-name {
  display: flex;
  align-items: center;
  height: 5rem;
  color: #F4DECB;
  text-decoration: none;
  -webkit-filter: grayscale(50%) opacity(0.7);
          filter: grayscale(50%) opacity(0.7);
  transition: var(--transition-speed);
}

.site-name:hover {
  -webkit-filter: grayscale(0%) opacity(1);
          filter: grayscale(0%) opacity(1);
  background: #603861;
  color: #F4DECB;
}

.link-text {
  display: none;
  margin-left: 1rem;
}

.site-name svg, .ic, .lang {
  width: 2rem;
  min-width: 2rem;
  margin: 0 1.5rem;
}

.logo {
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 1rem;
  text-align: center;
  color: #F4DECB;
  background: #49274A;
  font-size: 1.5rem;
  letter-spacing: 0.3ch;
  width: 100%;
}

.logo svg {
  transform: rotate(0deg);
  transition: var(--transition-speed);
}

.logo-text
{
  display: inline;
  position: absolute;
  left: -999px;
  transition: var(--transition-speed);
}

.sidebar:hover .logo svg {
  transform: rotate(-180deg);
}

/* MODAL */
.ReactModal__Overlay {
  opacity: 0;
  transition: opacity 700ms ease-in-out;
}

.ReactModal__Overlay--after-open{
  opacity: 1;
}

.ReactModal__Overlay--before-close{
  opacity: 1;
}

.close {
  background-color: transparent;
  color: black;
  padding: 5px;
  margin: 10px;
  border-radius: 4px;
  border: 2px solid #94618E;
  box-shadow: black;
  text-decoration: none;
  font-size: 13px;
  transition: 0.2s;
}
.close:hover{
  background-color: #94618E;
  color:#F4DECB;
  transform: scale(1.3); 
}
.cbtn {
  background-color: #49274A;
  color: #F4DECB;
  padding: 10px;
  margin: 10px;
  border-radius: 4px;
  border: 2px solid #49274A;
  box-shadow: black;
  text-decoration: none;
  font-size: 20px;
  transition: 0.2s;
}
.cbtn:hover{
  background-color: #94618E;
  color:#F4DECB;
  transform: scale(1.2);  
}


/* MOBILE */
@media only screen and (max-width: 600px){
  .sidebar {
    bottom: 0;
    width: 100%;
    height: 5rem;
  }

  .logo {
    display: none;
  }

  .side-nav {
    flex-direction: row;
  }

  .site-name {
    justify-content: center;
  }

  

}

/*LARGE SCREENS*/
@media only screen and (min-width: 600px){

  .sidebar {
    top: 0;
    width: 5rem;
    height: 100vh;
  }

  .sidebar:hover {
    width: 16rem;
  }

  .sidebar:hover .link-text {
    display: inline;
  }

  .sidebar:hover .logo svg
  {
    margin-left: 11rem;
  }

  .sidebar:hover .logo-text
  {
    left: 0px;
  }
  
}
