
.project-container {
  display: flex;
  flex-direction: row;
justify-content: space-evenly;
-webkit-box-align: center;
align-items: center;
flex-wrap: wrap;
margin-left: 5rem;
}

.project {
  display: flex;
    justify-content: space-around;
    -webkit-box-align: center;
    align-items: center;
    flex-direction: column;
    flex: 1 0 20%;
    width: 22rem;
    height: 30rem;
    padding: 5%;
    margin: 15px;
    border-radius: 6px;
    transition: 0.4s;
    background-color: #F8EEE7;
    box-shadow: 1px 1px 10px 0 rgba(0,0,0,.1);
}
.project:hover{
  transform: scale(1.07);
}
.project p{
  font-size: 15px;
}

.pbtn {
  background-color: #49274A;
  color: #F4DECB;
  padding: 10px;
  margin: 10px;
  border-radius: 4px;
  border: 2px solid #49274A;
  box-shadow: black;
  text-decoration: none;
  font-size: 20px;
  transition: 0.2s;
}
.pbtn:hover{
  background-color: #94618E;
  color:#F4DECB;
  transform: scale(1.1); 
}


.ecom {
  height:50%;
  width:80%;
  border-radius: 4px;
  box-shadow: 1px 1px 10px 0 #49274A;
}
.happ {
  width: 90%;
  box-shadow: 1px 1px 10px 0 #49274A;
  padding: 4px;
  border-radius: 2px;
  background-color: black;
}
.project h4 {
  font-size: 20px;
  margin-bottom: 5px;
}

@media (max-width: 800px){
  .project-container {
    display: grid;
    grid-template-rows: repeat(2, 1fr);
  }
}
@media (max-width: 600px){
  .project-container{
    margin:auto;
  }
}